<template>
  <div class="main">
    <!-- <div class="timeCut flexCenter">10:00</div> -->
    <img
      src="./../assets/images/shake.png"
      v-if="hasJoin"
      class="center_img_shake"
      alt=""
    />
    <!-- <div class="flexCenter center_text_ready">请准备</div>
    <div class="flexCenter center_text_message">速度竞赛</div> -->
    <div class="flexCenter fsff" v-else>请先扫码参与</div>
    <div class="pop_box">
      <AwardPop v-show="showAward"></AwardPop>
    </div>
  </div>
</template>

<script>
import {
  reactive,
  onMounted,
  toRefs,
  onDeactivated,
  onUnmounted,
  onActivated,
} from "vue";
import AwardPop from "./../components/AwardPop.vue";
import { shake } from "./../lib/shake";
import request from "../api/request";
import { useRoute } from "vue-router";
export default {
  name: "demo",
  components: {
    AwardPop,
  },
  setup(props) {
    let state = reactive({
      showAward: false,
      hasJoin: false,
    });
    let route = useRoute();
    if (route.query.activityId) {
      sessionStorage.setItem("activityId", route.query.activityId);
      state.activityId = route.query.activityId;
    } else {
      let activityId = sessionStorage.getItem("activityId");
      if (activityId) {
        state.activityId = activityId;
      }
    }
    if (route.query.shakeRoundId) {
      // 参数携带roundId
      sessionStorage.setItem("shakeRoundId", route.query.shakeRoundId);
    }
    async function checkJoin() {
      if (route.query.shakeRoundId) {
        // 参数携带roundId
        sessionStorage.setItem("shakeRoundId", route.query.shakeRoundId);
        state.shakeRoundId = route.query.shakeRoundId;
        state.hasJoin = true;
        // 摇一摇签到
        let resp = await request.get("/api/v1/user/sign/signForShake", {
          openid: sessionStorage.getItem("openid"),
          roundId: route.query.shakeRoundId,
        });
      } else {
        let roundId = sessionStorage.getItem("shakeRoundId");
        if (!roundId) {
          state.hasJoin = false;
        } else {
          // 摇一摇签到
          state.shakeRoundId = roundId;
          let resp = await request.get("/api/v1/user/sign/signForShake", {
            openid: sessionStorage.getItem("openid"),
            roundId: roundId,
          });
        }
      }
    }

    let sendCount = function (count) {
      console.log(
        count,
        "countSend",
        route.query.shakeRoundId || sessionStorage.getItem("shakeRoundId")
      );
      if (count == 0) {
        request.post("/api/v1/user/shake/uploadUserShakeData", {
          activityId: sessionStorage.getItem("activityId"),
          count: 1,
          openid: sessionStorage.getItem("openid"),
          roundId: route.query.shakeRoundId || sessionStorage.getItem("shakeRoundId"),
        });
      } else {
        request.post("/api/v1/user/shake/uploadUserShakeData", {
          activityId: sessionStorage.getItem("activityId"),
          count: count,
          openid: sessionStorage.getItem("openid"),
          roundId: route.query.shakeRoundId || sessionStorage.getItem("shakeRoundId"),
        });
      }
    };
    onMounted(async () => {
      let data = {
        activityId: route.query.activityId,
        openid: sessionStorage.getItem("openid"),
      };
      checkJoin();
    });
    onActivated(() => {
      shake.init(sendCount);
    });
    onDeactivated(() => {
      shake.destry();
    });
    return {
      ...toRefs(state),
    };
  },
};
</script>
<style lang="scss" scoped>
.main {
  width: 100%;
  height: 100%;
  background: url("./../assets/images/shake_bg.png") no-repeat center center;
  background-size: 100% 100%;
  overflow: hidden;
  padding-top: 650px;
  .timeCut {
    width: 280px;
    height: 70px;
    border-radius: 35px;
    border: 3px solid #ffffff;
    margin: 135px auto 362px auto;
    color: #ffffff;
    font-size: 37px;
  }
  @keyframes imgShake {
    0% {
      transform: rotate(0deg);
    }
    25% {
      transform: rotate(30deg);
    }
    50% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(-30deg);
    }
  }
  .center_img_shake {
    width: 164px;
    height: 164px;
    margin: 0 auto 21px auto;
    animation: imgShake 0.2s infinite;
  }
  .center_text_ready {
    font-size: 90px;
    font-family: FZXiYuan-M01S;
    font-weight: 400;
    color: #ffffff;
    line-height: 51px;
  }
  .center_text_message {
    color: #ffffff;
    font-size: 36px;
    margin-top: 24px;
  }
}
.flexCenter {
  display: flex;
  align-items: center;
  justify-content: center;
}
.fsff {
  color: #ffffff;
  font-size: 36px;
}
</style>
