
function Shake() {
    this.count = 0;
    this.x = 0
    this.y = 0;
    this.lastx = 0;
    this.lasty = 0;
    this.motion = null;
    this.clean = false;
    this.init = function (sendCallBack) {
        var self = this;
        function getCount() {
            if (!self.clean) {
                setTimeout(getCount, 2000);
            }

            console.log(self.count, 'countTime');
            if (sendCallBack) {
                sendCallBack(self.count);
                setTimeout(() => {
                    self.count = 0;
                }, 0);
            }
        }
        getCount();

        this.permission();
        // if (!window.DeviceMotionEvent) {
        //     alert("请更换手机")
        // } else {
        //     console.log('支持devicemotion', window.DeviceMotionEvent);
        //     window.addEventListener('devicemotion', function (event) {
        //         let speed = Math.floor(event.acceleration.x);
        //         if (speed > 1) {
        //             self.count += 1;
        //             console.log(Math.abs(event.acceleration.x) + ' m/s2', event);
        //         }

        //     }, false);
        // }

    }
    this.send = function (event) {

    }
    this.destry = function (event) {
        this.clean = true;
    }
    this.permission = function () {
        var self = this;
        if (typeof window.DeviceMotionEvent !== "undefined" && typeof window.DeviceMotionEvent.requestPermission === "function") {
            console.log("支持 requestPermission");
            if (confirm("ios 系统需要获取方向权限")) {
                console.log('用户点击同意')
                document.addEventListener('click',getPemiss,false);
                function getPemiss() {
                    window.DeviceMotionEvent.requestPermission().then((response) => {
                        console.log(2, response)
                        if (response == "granted") {
                            console.log(3)
                            window.addEventListener("devicemotion", (event) => {
                                let speed = Math.abs(event.acceleration.x);
                                speed = parseInt(speed);
                                console.error('速度 speed1', speed);
                                if (speed > 1) {
                                    self.count += 1;
                                } else {
                                }
                            });
                        } else {
                            console.log('获取权限失败');
                        }
                    }).catch(err => {
                        console.log('获取重力事件失败', err);
                    });
                }

            }

        } else {
            if (typeof window.DeviceMotionEvent !== "undefined") {
                // 不需要申请权限
                console.log('不需要申请权限')
                window.addEventListener("devicemotion", (event) => {
                    let speed = Math.abs(event.acceleration.x);
                    speed = parseInt(speed);
                    console.error('速度 speed2', speed);
                    if (speed > 1) {
                        self.count += 1;
                    } else {
                        self.count = 1;
                    }
                });
            } else {
                console.log('手机不支持重力方法');
            }

        }
    }
    function getIos() {

        var u = window.navigator.userAgent;

        return !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/);

    }
    function iosgetPermiss() {
        if (getIos()) {
            if (typeof window.DeviceMotionEvent !== "undefined" && typeof window.DeviceMotionEvent.requestPermission === "function") {
                console.log("支持 requestPermission")
                window.DeviceMotionEvent.requestPermission().then((response) => {
                    console.log(2, response)
                    if (response == "granted") {
                        console.log(3)
                        window.addEventListener("devicemotion", (event) => {
                            let speed = Math.abs(event.acceleration.x);
                            speed = parseInt(speed);
                            console.error('速度 speed1', speed);
                            if (speed > 1) {
                                self.count += 1;
                            } else {
                            }
                        });
                    } else {
                        console.log('获取权限失败');
                    }
                }).catch(err => {
                    console.log('获取重力事件失败', err);
                });
            } else {
                if (typeof window.DeviceMotionEvent !== "undefined") {

                    // 不需要申请权限
                    console.log('不需要申请权限')
                    window.addEventListener("devicemotion", (event) => {
                        let speed = Math.abs(event.acceleration.x);
                        speed = parseInt(speed);
                        console.error('速度 speed2', speed);
                        if (speed > 1) {
                            self.count += 1;
                        } else {
                            self.count = 1;
                        }
                    });
                } else {
                    console.log('手机不支持重力方法');
                }

            }
        }
    }

}
export const shake = new Shake();